.other_days_block {
    margin-top: 5rem;
    display: flex;
    align-items: center;
    justify-content: space-between;

    @media only screen and (max-width: 1200px) {
        justify-content: unset;
        overflow-x: scroll;
    }

    .other-day-item {
        width: 18%;
        display: flex;
        flex-direction: column;
        background-color: rgba(255, 255, 255, 0.2);
        padding: 2rem;
        border-radius: 10px;

        @media only screen and (max-width: 1200px) {
            width: 200px;
            margin-right: 20px;
        }

        &:last-child {
            margin-right: 0;
        }

        .weather-day {
            font-size: 3.2rem;
            text-align: center;
            margin-bottom: 2rem;
        }

        .wrap {
            display: flex;
            align-items: center;

            .weather-icon-small {
                width: 10rem;
                height: 10rem;
                margin-right: 2rem;
            }

            .text {
                display: flex;
                flex-direction: column;
                align-items: center;

                .temp {
                    font-size: 5rem;
                }

                .desc {
                    font-size: 2.6rem;
                    text-transform: capitalize;
                }
            }
        }
    }
}